html {
  background: linear-gradient(to left, #181a1b, #111613);
  font-family: Montserrat, sans-serif;
}

body {
  margin: 0;
}

.allOptionsSection {
  color: white;
  width: 340px;
  margin: 0 auto;
}

.allOptionsSectionTitle {
  text-align: center;
  margin: 0;
  margin-bottom: 20px;
  font-size: 34px;
  font-weight: normal;
  line-height: 1.6;
}

.allOptionsSectionSpan {
  display: block;
  line-height: 1.4;
  font-size: 26px;
  font-weight: normal;
  margin-bottom: 20px;
}

.allOptionsSectionP {
  line-height: 1.6;
  margin-bottom: 20px;
}

.allOptionsSectionList {
  margin: 0;
  padding: 0;
}

@media (min-width: 768px) {
  .allOptionsSection {
    padding-top: 175px;
    width: 785px;
  }

  .allOptionsSectionTitle {
    font-size: 80px;
  }

  .allOptionsSectionSpan {
    line-height: 3;
  }
}
